import React from "react";
import './body.styles.css';

export function Body(props) {
    
    const isDark = props.mode;
    if (isDark) { 
        return (
            <div style={{height: '5000px', color: 'white'}} className="Dark-bg">
                <h1 className="Name">JAMES GABBITUS</h1>
                
                <a href="#page-1" style={{height: '370px', color: 'lightgrey'}}>ABOUT</a>
                <a href="#page-2" style={{height: '800px', color: 'lightgrey'}}>PROJECTS</a>
                <a href="#page-3" style={{height: '1200px', color: 'lightgrey'}}>EDUCATION</a>
                <div class="material-icons-expand-1">
                    <a href="#page-1" class="material-icons" style={{color: 'white'}}>expand_more</a>
                </div>

                <div class="scroll-page" id="page-1">
                    <div id="about" style={{color: 'lightgrey'}}>
                        <p id="sections" style={{color: 'white'}}>ABOUT</p>
                        James is a full stack developer with a keen interest
                        in web development, UI/UX design and best development practices. He loves to explore
                        new and evolving technologies so that he can learn and implement them in his projects.
                    </div>
                </div>
                <div class="scroll-page" id="page-2"><p id="sections">PROJECTS</p></div>
                <div class="scroll-page" id="page-3"><p id="sections">EDUCATION</p></div>
            </div>
        );
    }
    return (
        <div style={{height: '5000px'}} className="Light-bg">
            <h1 className="Name">JAMES GABBITUS</h1>
            
            <a href="#page-1" style={{height: '370px'}}>ABOUT</a>
            <a href="#page-2" style={{height: '800px'}}>PROJECTS</a>
            <a href="#page-3" style={{height: '1200px'}}>EDUCATION</a>
            <div class="material-icons-expand-1">
                <a href="#page-1" class="material-icons" style={{color: 'black'}}>expand_more</a>
            </div>

            <div class="scroll-page" id="page-1">
                <div id="about" style={{color: 'rgb(73, 63, 63)'}}>
                    <p id="sections" style={{color: 'black'}}>ABOUT</p>
                     James is a keen full stack developer with an interest
                    in web development, UI/UX design and best practices. He loves to explore
                    new and evolving technologies so that he can implement them in his projects.
                </div>
            </div>
            <div class="scroll-page" id="page-2"><p id="sections">PROJECTS</p></div>
            <div class="scroll-page" id="page-3"><p id="sections">EDUCATION</p></div>
        </div>
        
    );
    
}